import DataSheetBase from './DataSheetBase.js';

export default class DataSheet_food extends DataSheetBase {

  constructor(id, updateCb) {
    super(id, updateCb);
    this.requestedKeyPath = "";  // this value can be specified in the React Studio data sheet UI
  }

  makeDefaultItems() {
    // eslint-disable-next-line no-unused-vars
    let key = 1;
    // eslint-disable-next-line no-unused-vars
    let item;

    // The contents of this data sheet will be loaded by plugin 'Google Sheets'.
    
    item = {};
    this.items.push(item);
    item['email'] = "";
    item['id'] = "https://spreadsheets.google.com/feeds/list/1c5cwcBt5-pGw-l8uemTFsPh1obRseYhaoOThY6qTuic/od6/cokwr";
    item['title'] = "Northern Illinois Food Bank";
    item['ids'] = "1";
    item['desc'] = "Northern Illinois Food Bank is offering support, including a list of the food pantries and programs nearest you";
    item['link'] = "https://solvehungertoday.org/get-help/where-to-get-food/";
    item.key = key++;
    
    item = {};
    this.items.push(item);
    item['email'] = "";
    item['id'] = "https://spreadsheets.google.com/feeds/list/1c5cwcBt5-pGw-l8uemTFsPh1obRseYhaoOThY6qTuic/od6/cpzh4";
    item['title'] = "SNAP";
    item['ids'] = "2";
    item['desc'] = "SNAP Illinois has established additional services for people who have lost income and/or may need to apply for SNAP as well as resources for people who are considered \"essential\" and are still working. If you live in the city of Rockford, you can call the city's department of Human Resources at 815-987-7620 and they will direct to more support in the city.";
    item['link'] = "https://www.dhs.state.il.us/page.aspx?module=12&officetype=&county=Winnebago";
    item.key = key++;
  }

  
  _fetchComplete = (err, options) => {
    if (err) {
      console.log('** Web service write failed: ', err, options);
    } else {
      if (this.updateCb) this.updateCb(this);
    }
  }
  
  _makeUrl(options, subpath) {
    const baseUrl = "https://apibridge.neonto.com/api";
    let path = options.servicePath || '';
    if (path.length > 0 && path.substr(0, 1) !== '/' && baseUrl.substr(baseUrl.length - 1, 1) !== '/')
      path = '/'+path;
    
    return baseUrl + "/gsheet" + path + subpath;
  }
  
  addItem(item, options) {
    super.addItem(item, options);
    
    const url = this._makeUrl(options, "/rows");
    
    const fetchOpts = {
      method: 'POST',
    	body: JSON.stringify({row: item}),
    	headers: {
    	  'Content-Type': 'application/json',
    	}
    };
  
    fetch(url, fetchOpts)
      .then((response) => {
        if (response.status >= 400) {
          throw new Error("Server error: "+response.status);
        }
        return response.json();
      })
      .then((json) => {
    		console.log("gsheet data save complete");
        this._fetchComplete(null, options);
      })
      .catch((exc) => {
        this._fetchComplete(exc, options);
      });  
  }
  
  
  removeItem(item, options) {
    super.removeItem(item, options);
    
    this._fetchComplete(null, options);
  }
  
  replaceItemByRowIndex(idx, newItem, options) {
    super.replaceItemByRowIndex(idx, newItem, options);
    
    this._fetchComplete(null, options);
  }

}
